.cnt {
    padding-top: 10px;
}
.form {
    display: flex;
    width: 93vw;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
    gap: 20px;
}

.form-container {
    display:flex;
    flex-direction: column;
    align-items: center;
}

.form input {
    width: 20vw;
    border: none;
    outline: none;
    border-bottom: 1px solid rgba(180, 180, 180, 0.6);
    background: #0e1538;
    padding: 10px;
    color: #fff;
}
.form input:focus {
    border-bottom: 1px solid rgba(73, 73, 73, 0.6);
}
.form input::placeholder {
    color: rgb(163, 163, 163);
}

.form ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style: none;
}

.form li {
    display: absolute;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    padding: 10px;
    background-color: #4187f6;
    width: 200px;
    height: 50px;
}
.form a {
    color: #fff;
    text-decoration: none;
}

.form img {
    object-fit: cover;
    opacity: 0.7;
    width: 200px;
    max-height: 290px;
    min-height: 290px;
    margin-left: 36px;
}

.form img:hover {
    opacity: 1;
}

.portada {
    position: relative;
}

.bottomLeft {
    position: absolute;
    bottom: 105px;
    left: 16px;
    font-size: 14px;
  }

button {
    display: flex;
    border: none;
    margin: 20px 0;
}

button.fav {
    margin-left: 56px;
}

.boton div {
    position: relative;
    width: 160px;
    height: 60px;
    display: inline-block;
    background: #fff;
}

.boton div:before,
.boton div:after {
    content: "";
    position: absolute;
    inset: 0;
    background: #f00;
    transition: 0.5s;
    background: linear-gradient(45deg, #00ccff, #0e1538, #0e1538, #d400d4);
}

/* 
.boton div:nth-child(1):before,
.boton div:nth-child(1):after {
    background: linear-gradient(45deg, #00ccff, #0e1538, #0e1538, #d400d4);
    background: linear-gradient(45deg, #ff075b, #0e1538, #0e1538, #1aff22); 
} 
*/


.boton div:hover:before {
    inset: -3px;
}

.boton div:hover:after {
    inset: -3px;
    filter: blur(10px);
}

.boton div span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    background: #0e1538;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    border: 1px solid #040a29;
    overflow: hidden;
}

.boton div span::before {
    content: "";
    position: absolute;
    top: 0;
    left: -50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.075);
    transform: skew(25deg);
}